  padding: 5px 20px; }

/*****************************************************************************/
/*
/* Misc
/*
/*****************************************************************************/
.text {
  word-wrap: break-word;
  -webkit-hyphens: auto;
  -moz-hyphens: auto;
  hyphens: auto; }

#related {
  list-style: none;
  padding-top: 40px;
  text-align: center; }

#related li {
  width: 31%;
  padding: 0 1%;
  display: block;
  float: left;
  text-align: center; }

#related > span,
#related > a {
  white-space: nowrap;
  line-height: 1.43; }

#related li > a {
  font-size: 1.5em;
  text-decoration: none; }

#related li p {
  margin: 5px 0 0 0;
  text-align: left; }

#related > div.title:only-child {
  display: none; }

#related span:first-child:after {
  content: ", ";
  margin: 0 4px 0 -2px; }

#related span:last-child:after {
  content: ".";
  margin-left: -3px; }

#disqus_thread {
  max-width: 768px; }

.small {
  max-width: 420px; }

/*
* Media Queries 
*/
@media screen and (max-width: 1024px) {
  .unit.one-fifth {
    width: 33%; } }

@media screen and (max-width: 767px) {
  .video-container {
    display: none; }
  .header-wrapper img {
    width: 32px !important; }
  #content,
  .blog .half,
  .blog .content-wrapper {
    width: 100% !important; } }

@media screen and (max-width: 532px) {
  .unit.one-fifth {
    width: 50%; }
  .flickr li,
  .flickr a {
    width: 153.3px;
    height: 153.3px; }
  #what-ppl-say h3 {
    left: 22px; } }

@media screen and (max-width: 480px) {
  .padding-top,
  .more-padding-top {
    padding-top: 30px !important; }
  #grid[data-columns]::before {
    content: '1 .column.size-1of1'; }
  .post-wrapper,
  .header-wrapper {
    margin-left: 10px !important;
    margin-right: 10px !important; }
  .grid {
    padding: 0; }
  .github .unit.one-fifth {
    width: 100%; }
  .projects .half {
    padding-bottom: 100% !important; }
  .half,
  .grid .half,
  .grid .w-1-2,
  .golden-large {
    width: 100%; }
  .flickr li {
    width: 33.3% !important;
    height: auto; } }

@media screen and (min-width: 481px) and (max-width: 768px) {
  #grid[data-columns]::before {
    content: '2 .column.size-1of2'; } }

@media screen and (min-width: 769px) {
  #grid[data-columns]::before {
    content: '3 .column.size-1of3'; } }

.size-1of1 {
  width: 100%; }

.size-1of2 {
  width: 50%; }

.size-1of3 {
  width: 33.333%; }
